 10px;\r\n}\r\n\r\n.content .content__heading a {\r\n  text-decoration: none;\r\n  color: black;\r\n}\r\n\r\n#query {\r\n  margin-left: auto;\r\n  margin-right: auto;\r\n  display: flex;\r\n  padding: 16px;\r\n  text-align: center;\r\n}\r\n\r\n/* restaurants */\r\n\r\n.restaurants {\r\n  display: grid;\r\n  grid-template-columns: 1fr;\r\n  gap: 16px;\r\n  margin: 32px 0;\r\n}\r\n\r\n/* restaurant Item */\r\n\r\n.restaurant-item {\r\n  width: 100%;\r\n  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\r\n  border-radius: 8px;\r\n  overflow: hidden;\r\n}\r\n\r\n.restaurant-item__header {\r\n  position: relative;\r\n}\r\n\r\n.restaurant-item .restaurant-item__header #restaurant-item__header__poster {\r\n  width: 100%;\r\n}\r\n\r\n.restaurant-item .restaurant-item__header .restaurant-item__header__rating {\r\n  position: absolute;\r\n  padding: 8px;\r\n  bottom: 20px;\r\n  left: 0;\r\n  display: inline-block;\r\n  background-color: black;\r\n  color: white;\r\n}\r\n\r\n.restaurant-item\r\n  .restaurant-item__header\r\n  .restaurant-item__header__rating\r\n  .restaurant-item__header__rating__score {\r\n  margin-left: 10px;\r\n}\r\n\r\n.restaurant-item .restaurant-item__content {\r\n  padding: 16px;\r\n  margin-top: 8px;\r\n}\r\n\r\n.restaurant-item .restaurant-item__content h3 {\r\n  margin: 0 0 8px 0;\r\n  text-align: center;\r\n}\r\n\r\n.restaurant-item .restaurant-item__content h3 a {\r\n  color: #db0000;\r\n  text-decoration: none;\r\n  padding: 15px;\r\n}\r\n\r\n.restaurant-item .restaurant-item__content h4 {\r\n  margin: 16px 0 8px 0;\r\n  text-align: center;\r\n  text-decoration: none;\r\n}\r\n\r\n.restaurant-item .restaurant-item__content p {\r\n  overflow: hidden;\r\n  text-overflow: ellipsis;\r\n  display: -webkit-box;\r\n  -webkit-line-clamp: 4; /* number of lines to show */\r\n  -webkit-box-orient: vertical;\r\n}\r\n\r\n/* restaurant */\r\n\r\n.restaurant {\r\n  margin: 0 auto;\r\n  width: 100%;\r\n  max-width: 800px;\r\n  display: grid;\r\n  grid-template-columns: 1fr;\r\n  gap: 18px 16px;\r\n}\r\n\r\nh2.restaurant__name {\r\n  text-align: center;\r\n  margin-bottom: 12px;\r\n}\r\n\r\nimg.restaurant__poster {\r\n  max-width: 80vw;\r\n  display: block;\r\n  margin-right: auto;\r\n  margin-left: auto;\r\n}\r\n\r\n.restaurant__info {\r\n  margin-top: 16px;\r\n  margin-bottom: 4px;\r\n  display: grid;\r\n  gap: 4px;\r\n}\r\n\r\n.restaurant__info h3 {\r\n  text-align: center;\r\n}\r\n\r\n.restaurant .restaurant__info h4 {\r\n  margin: 16px 0;\r\n}\r\n\r\n.restaurant-overview {\r\n  margin-bottom: 4px;\r\n}\r\n\r\n/* restaurant-menu-foods */\r\n\r\n.restaurant-menu-foods {\r\n  display: grid;\r\n  grid-template-columns: 1fr 1fr;\r\n  gap: 32px;\r\n  margin: 16px 0 32px 0;\r\n}\r\n\r\n.foods {\r\n  width: 100%;\r\n  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\r\n  border-radius: 8px;\r\n  padding: 8px;\r\n  text-align: center;\r\n}\r\n\r\n/* restaurant-menu-drinks */\r\n\r\n.restaurant-menu-drinks {\r\n  display: grid;\r\n  grid-template-columns: 1fr 1fr;\r\n  gap: 32px;\r\n  margin: 16px 0 32px 0;\r\n}\r\n\r\n.drinks {\r\n  width: 100%;\r\n  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\r\n  border-radius: 8px;\r\n  padding: 8px;\r\n  text-align: center;\r\n}\r\n\r\n/* customer-review */\r\n\r\n.customer-review {\r\n  display: grid;\r\n  grid-template-columns: 1fr;\r\n  gap: 16px;\r\n  margin: 16px 0 32px 0;\r\n}\r\n\r\n.review-item {\r\n  width: 100%;\r\n  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\r\n  border-radius: 8px;\r\n  padding: 8px;\r\n}\r\n\r\n/* Like */\r\n\r\n.like {\r\n  width: 55px;\r\n  height: 55px;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  background-color: #db0000;\r\n\r\n  position: fixed;\r\n  bottom: 16px;\r\n  border-radius: 50%;\r\n  border: 0;\r\n\r\n  font-size: 18px;\r\n  color: white;\r\n  cursor: pointer;\r\n}\r\n\r\n/* Footer */\r\n\r\nfooter {\r\n  padding: 16px;\r\n  text-align: center;\r\n}\r\n\r\nfooter p {\r\n  text-align: center;\r\n  color: #aaaaaa;\r\n}\r\n\r\nfooter p a {\r\n  color: #db0000;\r\n  text-decoration: none;\r\n}\r\n\r\n#about {\r\n  padding-top: 10px;\r\n  padding-bottom: 10px;\r\n  display: block;\r\n  border-top: 5px solid black;\r\n  border-bottom: 5px solid black;\r\n  margin-bottom: 6px;\r\n}\r\n\r\n.social-media-list {\r\n  margin: 0 auto;\r\n  display: inline-block;\r\n}\r\n\r\n.social-media-item {\r\n  display: inline-block;\r\n  margin: 0 1em;\r\n  font-size: 14px;\r\n}\r\n\r\n.social-media-item a {\r\n  display: inline-block;\r\n  padding: 1.3em;\r\n  text-decoration: none;\r\n  margin-top: 1em;\r\n  margin-bottom: 1em;\r\n  transition: 0.3s opacity;\r\n}\r\n\r\n.social-media-item a:hover {\r\n  opacity: 0.5;\r\n}\r\n\r\n.social-media-item a:focus {\r\n  border: 5px solid black;\r\n  border-radius: 5px;\r\n}\r\n\r\n.social-media-item a p {\r\n  color: black;\r\n  font-weight: bold;\r\n}\r\n\r\n.fa-github {\r\n  padding: 5px 7px;\r\n  color: #fff;\r\n  background-color: rgb(30, 48, 80);\r\n}\r\n\r\n.fa-linkedin {\r\n  padding: 5px 7px;\r\n  color: #fff;\r\n  background-color: #0073a4;\r\n}\r\n"],"sourceRoot":""}]);
